<template>
	<view class="bg-white relative">
		<block v-for="(item, index) in tzList" :key="index">
			<div class="pb-1">
				<div class="px-4">				
					<content v-if="item.name === 'content'" :data="item" :child="item.child" />
				</div>
				<hero v-if="item.name === 'hero'" :data="item" :child="item.child" />
				<grid v-if="item.name === 'grid'" :data="item" :child="item.child" />				
				<titles v-if="item.name === 'title'" :data="item" :child="item.child" />
				<cards v-if="item.name === 'cards'" :data="item" :child="item.child" />
				<us v-if="item.name === 'us'" :data="item" :child="item.child" /> 
				<lists v-if="item.name === 'lists'" :data="item" :child="item.child" />
				<pros v-if="item.name === 'pros'" :data="item" :child="item.child" />
				<ask v-if="item.name === 'ask'" :data="item" :child="item.child" />
				<count v-if="item.name === 'count'" :data="item" :child="item.child" /> 
			</div>
		</block>
		<div class="text-gray-400 text-sm text-center mt-2">由 轻松自助建站 搭建 </div>
		<div class="h-20"></div>
		<foot />
	</view>
</template>

<script setup>
import content from '@/components/diy/content/index.vue';
import hero from '@/components/diy/hero/index.vue';
import grid from '@/components/diy/grid/index.vue';
import pros from '@/components/diy/pros/index.vue';
import lists from '@/components/diy/lists/index.vue';
import cards from '@/components/diy/cards/index.vue';
import us from '@/components/diy/us/index.vue';
import ask from '@/components/diy/ask/index.vue';
import count from '@/components/diy/count/index.vue';
import titles from '@/components/diy/title/index.vue';
import foot from '@/components/footNav.vue';
import { apiCoverPageData } from '@/api/cover.js';  
import { onLoad } from '@dcloudio/uni-app'; 
const tzList = ref();
const route = useRoute();

onLoad((options)=>{
	if (options.id < 1) {
		return;
	}
	loadData(options.id);
}) 
const loadData = (id) => {
	apiCoverPageData(id).then(res => {
		tzList.value = res;
	});
};
</script>

